//import * as CONF from '@/config/config.js';
var tronWeb;
const rtTrx = {
    /**
     * @description: 判断浏览器是否支持window.tronWeb
     * @param {*}
     * @return {*}
     */
    init() {
        return new Promise((cback) => {
            if (window.tronWeb) {
                tronWeb = window.tronWeb;
                
                // 2. check node connection，检查所需要的API是否都可以连通
                const nodes = tronWeb.isConnected();
                const connected = !Object.entries(nodes).map(([name, connected]) => {
                    if (!connected) {
                        console.log(`Error: ${name} is not connected`);
                    }
                    return connected;
                }).includes(false);
                if (connected) {
                    cback(this.json(1, '加载成功', tronWeb));

                } else {
                    console.log(`Error: TRON node is not connected`);
                    console.log('wait for tronLink');
                    cback(this.json(0, `Error: TRON node is not connected`, []));
                }

            } else {
                // 如果检测到没有注入tronWeb对象，则等待100ms后重新检测
                //console.log('wait for tronLink');
                cback(this.json(0, `wait for tronLink`, []))
            }
        });

    },
    /**
     * @description: 查询账号余额
     * @param {string} address 查询地址
     * @return {object} 余额
     */
    async getBalance(address) {
        try {

            if (!tronWeb.isAddress(address)) throw '钱包地址错误';
            let balance = await tronWeb.trx.getBalance(address);
            return this.json(1, '获得余额', tronWeb.fromSun(balance));
        } catch (error) {
            return this.json(0, error, []);
        }
    },

    /**
     * @description: 转账TRX
     * @param {string} toAddr 转账地址 
     * @param {string} money 数量
     * @return {object} 
     */
    send(toAddr, money) {
        return new Promise((cback, reject) => {
            tronWeb.trx.sendTransaction(toAddr, tronWeb.toSun(money)).then(doc => {
                cback(this.json(1,'转账成功', doc));
            }).catch(err => {
                reject(this.json(0, '转账出错', err))
            })
        });
    },

    /**
     * @description: 合约注册 
     * @param {string} address 绑定上级 
     * @return {object} 
     */
    
    // register(address) {
        
    // },


    /**
     * @description: 定义返回JSON格式
     * @param {number} code 状态
     * @param {string} msg 消息说明
     * @param {*} data 内容
     * @return {object} {code: code,msg: msg,data: data}
     */
    json(code, msg, data = []) {
        return {
            code: code,
            msg: msg,
            data: data
        }
    },
    timeNow() {
		const timestamp = Date.parse(new Date());
		return timestamp / 1000;
	}
}
export default rtTrx;