
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import * as Vant from "vant";
import 'vant/lib/index.css';

Vue.use(Vant);
Vue.prototype.$store = store;
Vue.config.productionTip = false
Vue.prototype.$vant = Vant;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
