const langJson = {
	'zh': require('../common/lang/zh'),
	'en': require('../common/lang/en'),
};
const setDeflang = 'en';
const mutations = {
	setlang(state) {
		const deflang = localStorage.getItem('systemdeflang') || setDeflang;
		state.rtlang = langJson[deflang];
	},
	uplang(state, langs){
		localStorage.setItem('systemdeflang',langs);
		state.rtlang = langJson[langs];
	}
}
export default mutations
