<script>
import rtTrx from "@/common/rtTrx.js";
import * as CONF from "@/config/config.js";
import ClipboardJS from "clipboard";
import { mapState, mapMutations } from "vuex";
export default {
  name: "Home",
  data() {
    return {
      langs: [
        { name: "中文", id: "zh" },
        { name: "EN", id: "en" },
      ],
      curLang: 0,
      isfenxiang: false,
      mybalance: "",
      upaddress: "",
      imshow: false,
      zlshow: false,
      jyshow: false,
      investData: {
        money: "",
      },
      withdrawData: {
        money: "",
      },
      tronWeb: {},
      contract: {},
      address: "",
      addrTonk: "",
      userinfo: {
        id: "--",
        valid: 0,
        line: "--",
        level: "--",
        ucount: 0,
        vcount: 0,
        invest: 0,
        validinvest: 0,
        amount: 0,
        reward: 0,
        validmoney: 0,
        reserve0: 0,
        reserve1: 0,
        reserve2: 0,
        reserve3: 0,
        reserve4: 0,
        time: "0",
        totalCount: 0,
        totalInvest: 0,
        totalFree: 0,
        totalReward: 0,
        totalCommission: 0,
        totalWithdraw: 0,
        tonkBalance: 0,
        islingqu: 0,
        uper: "--",
      },
    };
  },
  mounted() {
    this.upaddress = this.$route.params.code;
    const deflang = localStorage.getItem("systemdeflang") || "en";
    if (deflang == "en") {
      this.curLang = 1;
    } else {
      this.curLang = 0;
    }
    this.trxinit();
  },
  computed: {
    ...mapState(["rtlang"]),
  },
  methods: {
    ...mapMutations(["uplang", "setlang"]),
    changeLanguage(i) {
      this.curLang = i;
      this.uplang(this.langs[i]["id"]);
    },
    trxinit() {
      rtTrx.init().then(async (doc) => {
        if (doc.code == 1) {
          this.tronWeb = doc.data;
          this.addrTonk = CONF.ABI_ADDRESS;

          this.contract = await this.tronWeb.contract().at(this.addrTonk);
          this.address = this.tronWeb.defaultAddress.base58;
          await this.getBalance();
          await this.getUser();

          setInterval(() => {
            let _this = this;
            _this.getUser();
          }, 30000);
        } else {
          setTimeout(() => {
            this.trxinit();
          }, 1000);
        }
      });
    },
    async getBalance() {
      let balance = await rtTrx.getBalance(this.address);
      this.mybalance = balance.data;
    },
    async freeUser() {
      this.$vant.Toast.loading({
        message: this.rtlang["正在领取奖励..."],
        forbidClick: true,
        duration: 0,
      });
      await this.contract["freeUser"]()
        .send()
        .then(() => {
          this.$vant.Toast.clear();
          this.$vant.Toast.success(this.rtlang["操作成功！等待链上确认"]);
        })
        .catch(() => {
          this.$vant.Toast.clear();
        });
    },
    async getReward() {
      if (this.userinfo.islingqu <= 0) {
        this.$vant.Toast.fail(
          this.rtlang["当前地址已领取！请等待链上确认后刷新"]
        );
        return;
      }
      this.$vant.Toast.loading({
        message: this.rtlang["正在领取奖励..."],
        forbidClick: true,
        duration: 0,
      });
      await this.contract["getReward"]()
        .send()
        .then(() => {
          this.$vant.Toast.clear();
          this.userinfo.islingqu = 0;
          this.$vant.Toast.success(this.rtlang["操作成功！等待链上确认"]);
        })
        .catch(() => {
          this.$vant.Toast.clear();
        });
    },
    async register(upaddr) {
      this.$vant.Toast.loading({
        message: this.rtlang["正在绑定推荐人..."],
        forbidClick: true,
        duration: 0,
      });
      await this.contract["register"](upaddr)
        .send()
        .then(() => {
          
          this.$vant.Toast.clear();
          this.$vant.Toast.success(this.rtlang["操作成功！等待链上确认"]);
        })
        .catch(() => {
          this.$vant.Toast.clear();
        });
    },
    async invest() {
      try {
        if (this.investData.money == "") throw this.rtlang["请输入投资金额"];
        if (this.investData.money < 100)
          throw this.rtlang["投资金额不能小于100TRX"];

        this.$vant.Toast.loading({
          message: this.rtlang["正在投资"] + "...",
          forbidClick: true,
          duration: 0,
        });
        let money = this.tronWeb.toSun(this.investData.money);
        this.contract["invest"]()
          .send({
            callValue: money,
          })
          .then(() => {
            this.$vant.Toast.clear();
            this.$vant.Toast.success(this.rtlang["投资成功"] + "!");
          })
          .catch(() => {
            this.$vant.Toast.clear();
          });
      } catch (error) {
        this.$vant.Toast.fail(error);
      }
    },
    async withdraw() {
      try {
        if (this.withdrawData.money == "") throw this.rtlang["请输入提现金额"];
        this.$vant.Toast.loading({
          message: this.rtlang["正在提现"] + "...",
          forbidClick: true,
          duration: 0,
        });
        let money = this.tronWeb.toSun(this.withdrawData.money);
        this.contract["withdraw"](money)
          .send()
          .then(() => {
            this.$vant.Toast.clear();
            this.$vant.Toast.success(this.rtlang["提现成功"] + "!");
          })
          .catch(() => {
            this.$vant.Toast.clear();
          });
      } catch (error) {
        this.$vant.Toast.fail(error);
      }
    },
    async getUser() {
      try {
        let doc = await this.contract["getUser"]().call();
        let nowTime = rtTrx.timeNow();
        this.userinfo = {
          id: doc.i32[0], //id  已投资会员才有id
          valid: doc.i32[1], //0 无效 1有效 2出局
          line: doc.i32[2], //1区， 2区
          level: doc.i32[3], //第几层
          ucount: doc.i32[4], //推荐人数
          vcount: doc.i32[5], //有效人数

          invest: this.tronWeb.fromSun(doc.i256[0]), //总投资
          validinvest: this.tronWeb.fromSun(doc.i256[1]), //有效投资
          amount: this.tronWeb.fromSun(doc.i256[2]), //3倍收益
          reward: this.tronWeb.fromSun(doc.i256[3]), //已收益
          validmoney: this.tronWeb.fromSun(doc.i256[4]), //有效收益，可提现
          reserve0: this.tronWeb.fromSun(doc.i256[5]), //15代业绩
          reserve1: this.tronWeb.fromSun(doc.i256[6]), //15代奖励
          reserve2: this.tronWeb.fromSun(doc.i256[7]), //1，2顶点5%佣金
          reserve3: this.tronWeb.fromSun(doc.i256[8]), //静态释放
          reserve4: this.tronWeb.fromSun(doc.i256[9]), //保留2
          time: (this.tronWeb.toDecimal(doc.i256[10]) - nowTime) * 1000, //释放时间

          totalCount: this.tronWeb.toDecimal(doc.i2565[0]), //总人数
          totalInvest: this.tronWeb.fromSun(doc.i2565[1]), //总投资
          totalFree: this.tronWeb.fromSun(doc.i2565[2]), //总释放
          totalReward: this.tronWeb.fromSun(doc.i2565[3]), //总15代收益
          totalCommission: this.tronWeb.fromSun(doc.i2565[4]), //总佣金
          totalWithdraw: this.tronWeb.fromSun(doc.i2565[5]), //提币总额
          tonkBalance: this.tronWeb.fromSun(doc.i2565[6]), //提币总额
          islingqu: this.tronWeb.fromSun(doc.i2565[7]), //提币总额
          uper: this.tronWeb.address.fromHex(doc.up), //上级会员
        };

        this.isfenxiang = true;
      } catch (error) {
        if (this.upaddress == undefined) {
          this.$vant.Dialog.alert({
            title: this.rtlang["提示"],
            message: this.rtlang[
              "当前账号未绑定推荐人，请重新以推荐人地址打开"
            ],
          }).then(() => {
            // on close
          });
          return;
        }

        this.$vant.Dialog.confirm({
          title: this.rtlang["提示"],
          message: `${this.rtlang["是否绑定当前推荐人"]}："${this.upaddress}？\n ${this.rtlang["绑定后将无法更改"]}！\n ${this.rtlang["如需更改推荐人账号请输入推荐人链接重新进入网站"]}！ `,
        })
          .then(() => {
            this.register(this.upaddress);
          })
          .catch(() => {
            // on cancel
          });
      }
    },
    doCopy() {
      let _this = this;
      if (!this.isfenxiang) {
        _this.$vant.Toast(this.rtlang["无效推广码"]);
        return;
      }
      if (this.userinfo.validinvest <= 0) {
        _this.$vant.Toast(this.rtlang["未参与投资"]);
        return;
      }
      var clipboard = new ClipboardJS(".cpkw", {
        text: function () {
          return CONF.DEF_URL + _this.address;
        },
      });
      clipboard.on("success", () => {
        clipboard.destroy();
        _this.$vant.Toast.success(this.rtlang["复制成功"]);
      });
      clipboard.on("error", () => {
        console.log("该浏览器不支持自动复制");
        _this.$vant.Toast.fail(this.rtlang["复制失败"]);
        clipboard.destroy();
      });
    },
  },
  filters: {
    filtersAddress(val) {
      let adds = val.substr(0, 8) + "...";
      return adds;
    },
    filtersToFixed(val) {
      return val.toFixed(6);
    },
  },
};
</script>
<style lang="scss" scoped>
@import "./home.scss";
</style>
